.quantity {
  position: relative;
  width: 120px;
  display: flex;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
  @include response(666px) {
    width: 100px;
  }
  input {
    flex: 0 0 40px;
    width: 40px;
    height: 38px;
    color: var(--Primary-Black, #000);
    text-align: center;
    font-family: "Instrument Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    padding: 0;
    border: 0;
  }
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    flex: 1 1 auto;
    @include desktop-hover() {
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &.disabled {
      pointer-events: none;
      svg {
        stroke: #bebebe;
      }
    }
  }
  &__minus {
  }
  &__plus {
  }
}
