.cart-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100dvw;
  height: 100dvh;
  z-index: 10;
  transform: translateX(100%);
  opacity: 0;
  visibility: hidden;
  transition:
    opacity 0.3s ease-in-out,
    visibility 0.3s ease-in-out;
  &.active {
    opacity: 1;
    visibility: visible;
    transform: none;
  }
  &__overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--Mask-Black---30, rgba(0, 0, 0, 0.3));
  }
  &__box {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    z-index: 1;
    max-width: 480px;
    width: 100%;
    display: flex;
    flex-direction: column;
    transform: translateX(100%);
    transition: 0.2s ease-in-out;
    transition-delay: .1s;
    .cart-popup.active & {
      transform: translateX(0);
    }
  }
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    padding: 16px 24px;
    gap: 16px;
    flex: 0 0 auto;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__title {
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
  }
  &__close {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    cursor: pointer;
    @include desktop-hover() {
      svg {
        stroke: rgba(255, 61, 0, 0.8);
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__holder {
    position: relative;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  &__empty {
    position: relative;
    padding: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    font-size: 16px;
    line-height: 24px;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__label {
    position: relative;
    background: var(--main-70, #4d6562);
    padding: 12px 24px;
    color: var(--Primary-White, #fff);
    font-size: 16px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    flex: 0 0 auto;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__list {
    position: relative;
    flex: 1 1 auto;
    overflow-y: auto;
    overscroll-behavior: none;
    padding: 24px;
    @include response(1200px) {
      padding: 16px;
    }
  }
  &__item {
    position: relative;
    padding-top: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    align-items: flex-start;
    gap: 16px;
    @include response(1200px) {
      padding-top: 16px;
      padding-bottom: 16px;
    }
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      padding-bottom: 0;
      border-bottom: 0;
    }
  }
  &__item-img {
    position: relative;
    flex: 0 0 100px;
    width: 100px;
    height: 100px;
    overflow: hidden;
    border: 1px solid transparent;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: $red;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-box {
    position: relative;
    flex: 1 1 auto;
    overflow: auto;
  }
  &__item-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 4px;
    gap: 12px;
  }
  &__item-category {
    position: relative;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__item-remove {
    position: relative;
    display: flex;
    cursor: pointer;
    display: flex;
    @include desktop-hover() {
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #4d6562;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-name {
    display: block;
    text-decoration: none;
    margin-bottom: 4px;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    padding-right: 28px;
    height: 48px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__item-labels {
    position: relative;
    margin-bottom: 16px;
    color: var(--Main-Gray, #595959);
    line-height: 20px;
    word-spacing: 8px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding-right: 28px;
  }
  &__item-footer {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__item-input {
    position: relative;
    flex: 0 0 auto;
  }
  &__item-price {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    overflow: auto;
  }
  &__item-price-regular {
    position: relative;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__item-price-new {
    color: var(--Secondary-Red, #f70808);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__item-price-old {
    font-size: 14px;
    color: var(--main-50, #8f8f8f);
    line-height: 1;
    text-decoration: line-through;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__recommended {
    position: relative;
    flex: 0 0 auto;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 16px 24px;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__recommended-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    margin-bottom: 16px;
  }
  &__recommended-title {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__recommended-controls {
    position: relative;
    display: flex;
    gap: 4px;
    flex: 0 0 auto;
  }
  &__recommended-arrow {
    cursor: pointer;
    border-radius: 4px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 32px;
    width: 32px;
    height: 32px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: $red;
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &.swiper-button-disabled {
      pointer-events: none;
      border-color: #d3d3d3;
      svg {
        stroke: #d3d3d3;
      }
    }
  }
  &__recommended-slider {
    position: relative;
  }
  &__recommended-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 16px;
    border-radius: 8px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Primary-White, #fff);
    padding: 12px 16px 12px 12px;
  }
  &__recommended-item-img {
    position: relative;
    flex: 0 0 80px;
    width: 80px;
    height: 80px;
    overflow: hidden;
    border: 1px solid transparent;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: $red;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__recommended-item-box {
    position: relative;
    flex: 1 1 auto;
    overflow: auto;
  }
  &__recommended-item-category {
    position: relative;
    margin-bottom: 4px;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__recommended-item-name {
    display: block;
    text-decoration: none;
    margin-bottom: 8px;
    color: var(--Main-Gray, #595959);
    font-weight: 500;
    line-height: 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__recommended-item-footer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
  }
  &__recommended-item-price {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__recommended-item-price-regular {
    position: relative;
  }
  &__recommended-item-price-new {
    color: var(--Secondary-Red, #f70808);
  }
  &__recommended-item-price-old {
    font-size: 14px;
    color: var(--main-50, #8f8f8f);
    line-height: 1;
    text-decoration: line-through;
  }
  &__recommended-btn {
    min-width: 76px;
  }
  &__footer {
    position: relative;
    border-top: 1px solid #e9e9e9;
    padding: 16px 24px;
    background: #fff;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__row {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 8px;
  }
  &__cell {
    position: relative;
    width: 50%;
    &:last-child {
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: 26px;
      text-align: right;
    }
    &--big {
      &:first-child {
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
      }
      &:last-child {
        font-size: 22px;
        font-weight: 500;
        line-height: 30px;
      }
    }
  }
  &__controls {
    margin-top: 16px;
    display: flex;
    gap: 8px;
  }
  &__controls-btn {
    width: 100%;
  }
}
